	import noneClass from './none-class/none-class'
	import classGroupPopup from '@/components/class-group-popup/class-group-popup.vue'

	export default {
		name:'MyClass',
		components:{
			noneClass,
			classGroupPopup
		},
		data() {
			return {
				//班级id
				classID: null,
				classList: Array,
				fdWeChatGroup:'' //微信号
			}
		},
		
		created() {
			// 班级列表数据  包括全部班级
			uni.showLoading({
			title: "加载中",
			mask: true
          });
			this.$http.get(this.$api.homework.index.getClassList, {
					"Content-Type": "application/json;charset=UTF-8"
				})
				.then(res => {
					// console.log("班级", res.data)
					this.classList = res.data;
					// console.log(this.classList.length);
					let classList=[{id:"ALL",name:"全部班级"}];
					res.data.forEach(item => {
						item.list.forEach((val)=>{
							classList.push({
								id:val.id,
								name:val.name
							})
						})
					});
					uni.hideLoading();
					// 全部安排的班级选择列表数据
					this.$store.commit("getClassList", classList);
				})

		},
		methods: {
			checkClassDetails(e) {
				// console.log("点击班级", e.currentTarget.dataset)	
				this.classID = e.currentTarget.dataset.classid;
				let teachpiclist = e.currentTarget.dataset.teachpiclist;
				let coursePlanId=e.currentTarget.dataset.courseplanid;
				this.$store.commit("getTeacherPhoto", teachpiclist);
				// 点击班级带参跳转班级详情
				uni.navigateTo({
					//路由带参数
					url:`${this.$path.homework.allPlan.classDetails}?classID=${this.classID}&coursePlanId=${coursePlanId}`
				})
			},
			checkClassGroup(e){
				this.fdWeChatGroup=e.currentTarget.dataset.fdwechatgroup.trim();
				this.$refs.popup.open();
			},
			//检查微信号是否为空
			weChatGroup(item){
				let isok=false;
				if(item){
					if(item.trim()){
						isok=true;
					}else {
						isok=false
					}
				}
				return isok
			
			}
		}
	}
